downloadFile(report) {
      const oReq = new XMLHttpRequest()
      const URL = 'http://localhost:8080/download/t.pdf' // URL URL
      oReq.open('GET', URL, true)
      oReq.responseType = 'blob'
      oReq.onload = function () {
        const file = new Blob([oReq.response], {
          type: 'blob'
        })
