<template>
    <div class="login-container" style="margin-left: 500px">
        <!-- <form class="login-form">
            <input type="text" required="required" placeholder="Username" v-model.trim="User.username">
            <br />
            <input :type="passwordType" required="required" placeholder="Password" v-model.trim="User.password">
            <br />
            <button @click="submit">Login</button>
        </form> -->
        <div class="title-container">
            <h1 class="title">Login</h1>
        </div>
        <div style="padding-top: 10px">
            <el-form ref="loginForm" :model="loginForm" :rules="loginRules" label-width="80px" class="login-form"
                auto-complete="on">
                <!-- label-width="500px"> -->
                <el-form-item prop="userName" label="">
                    <el-col :span="10">
                        <el-input ref="userName" v-model="loginForm.userName" placeholder="" name="userName"
                            type="text" tabindex="1" auto-complete="on" />
                    </el-col>
                </el-form-item>

                <el-form-item prop="password" label="">
                    <el-col :span="10">
                        <el-input :key="passwordType" ref="password" v-model="loginForm.password" :type="passwordType"
                            placeholder="" name="password" tabindex="2" auto-complete="on"
                            @keyup.enter.native="login" />
                    </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button :loading="loading" type="primary" style="width:40%;" @click.native.prevent="login">
                    </el-button>
                </el-form-item>
                <el-form-item>
                    <router-link to="/register"></router-link>
                </el-form-item>
            </el-form>
        </div>
    </div>
</template>
<script>
import { encrypt } from '@/utils/encrypt'
import axios from 'axios'

export default {
    name: 'LoginIndex',
    // data() {
    //     return {
    //         User: {
    //             username: "jackchen",
    //             password: "abc123456"
    //         },
    //         passwordType: "password"
    //     }
    // },
    data() {
        return {
            loginForm: {
                username: "jackchen",
                password: "abc123456"
            },
            loginRules: {
                // userName: [{
                //     type: 'email',
                //     required: true,
                //     trigger: 'blur',
                //     // message: '' 
                // }],
                userName: [
                    { required: true, message: "", trigger: "blur" },
                    { min: 5, max: 14, message: " 5  14 ", trigger: "blur" }
                ],
                password: [{
                    required: true,
                    // message: '',
                    trigger: 'blur'
                }, {
                    pattern: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,20}$/, message: ', 8-20'
                }]
            },
            loading: false,
            passwordType: 'password',
            redirect: undefined
        }
    },
    methods: {
        login: function () {
            this.$refs['loginForm'].validate((valid) => {
                if (valid) {
                    const user = {
                        username: this.loginForm.userName,
                        password: encrypt(this.loginForm.password)
                    }
                    console.log(user.password)
                    console.log('***')
                    axios({
                        method: 'post',
                        url: this.$api.loginUrl,
                        data: user,
                    }).then(res => {
                        if (res.data.status === 200) {
                            console.log(res)
                            sessionStorage.setItem("username", user.username)
                            this.$message({
                                showClose: true,
                                message: '!',
                                type: 'success'
                            })
                            setTimeout(() => {
                                this.$router.push('/home')
                            }, 2000)
                        }
                        else {
                            console.log(res)
                            this.$message({
                                showClose: true,
                                message: '!',
                                type: 'error'
                            })
                        }

                    }).catch(err => {
                        console.log(err.response.data.message)
                    })
                }
            })
        }
    }
}
</script>