<template>
    <div class="register-container" style="margin-left: 500px">
        <h1>Register</h1>
        <!-- <form class="login-form">
            <input type="text" required="required" placeholder="Email" v-model.trim="User.username">
            <br />
            <input type="password" required="required" placeholder="Password" v-model.trim="User.password">
            <br />
            <input type="password" required="required" placeholder="Password" v-model.trim="User.password">
            <button @click="submit">Register</button>
        </form> -->
        <el-form ref="ruleForm" :model="ruleForm" :rules="rules" label-width="80px" autocomplete="off"
            :hide-required-asterisk="true" size="medium">
            <div style="padding-top: 10px">
                <el-form-item label="" prop="username">
                    <el-col :span="10">
                        <el-input v-model="ruleForm.username" placeholder="" />
                    </el-col>
                    <!-- <el-button :loading="codeLoading" :disabled="isDisable" size="small" round @click="sendMsg">
                    </el-button> -->

                    <!-- <span class="status">{{ statusMsg }}</span> -->
                </el-form-item>
                <!-- <el-form-item label="" prop="code">
                    <el-col :span="10">
                        <el-input v-model="ruleForm.code" maxlength="6" placeholder="" />
                    </el-col>
                </el-form-item> -->
                <el-form-item label="" prop="pwd">
                    <el-col :span="10">
                        <el-input v-model="ruleForm.pwd" type="password" />
                    </el-col>
                </el-form-item>
                <el-form-item label="" prop="cpwd">
                    <el-col :span="10">
                        <el-input v-model="ruleForm.cpwd" type="password" />
                    </el-col>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" style="width: 40%" @click="register"></el-button>
                </el-form-item>
            </div>
        </el-form>

    </div>
</template>
<script>
import { encrypt } from '@/utils/encrypt'
import axios from 'axios'
export default {
    name: 'RegisterIndex',
    data() {
        return {
            ruleForm: {
                username: '',
                pwd: '',
                cpwd: ''
            },
            rules: {
                // email: [{
                //     required: true,
                //     type: 'email',
                //     message: '',
                //     trigger: 'blur'
                // }],
                username: [
                    { required: true, message: "", trigger: "blur" },
                    { min: 5, max: 14, message: " 5  14 ", trigger: "blur" }
                ],
                // code: [{
                //   required: true,
                //   type: 'string',
                //   message: '',
                //   trigger: 'blur'
                // }],
                pwd: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }, { pattern: /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{8,20}$/, message: ', 8-20' }],
                cpwd: [{
                    required: true,
                    message: '',
                    trigger: 'blur'
                }, {
                    validator: (rule, value, callback) => {
                        if (value === '') {
                            callback(new Error(''))
                        } else if (value !== this.ruleForm.pwd) {
                            callback(new Error(''))
                        } else {
                            callback()
                        }
                    },
                    trigger: 'blur'
                }]

            }
        }
    },
    methods: {
        register: function () {
            this.$refs['ruleForm'].validate((valid) => {
                if (valid) {
                    const user = {
                        username: this.ruleForm.username,
                        password: encrypt(this.ruleForm.pwd)
                    }
                    console.log(user)
                    axios({
                        method: 'post',
                        url: this.$api.registerUrl,
                        data: user,
                    }).then(res => {
                        if (res.data.status === true) {
                            console.log(res)
                            this.$message({
                                showClose: true,
                                message: '...',
                                type: 'success'
                            })
                            setTimeout(() => {
                                this.$router.push('/login')
                            }, 2000)
                        }
                        else {
                            console.log(res)
                            this.$message({
                                showClose: true,
                                message: '!',
                                type: 'error'
                            })
                        }

                    }).catch(err => {
                        console.log(err.response.data.message)
                    })
                }
            })
        }

    }
}
</script>